body {
    padding: 0;
    margin: 0;
    color: #46494d;
    background-color: #f5f5f5;
    font-family: Turbine, sans-serif;

    &.popup-opened {
        overflow: hidden;

        #wrapper {
            filter: blur(10px);
        }
    }

    &.no-scroll {
        overflow: hidden;
    }

    * {
        box-sizing: border-box;
    }
}

* {
    box-sizing: border-box;
}

.custom-container {
    max-width: 1078px;
    margin: 0 auto;

    @media screen and (max-width: 1200px) {
        width: 1000px;
    }

    @media screen and (max-width: 992px) {
        width: 700px;
    }

    @media screen and (max-width: 768px) {
        width: 100%;
        padding: 0 24px;
    }
}

svg {
    pointer-events: none;
}

#wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow-x: hidden;
}

#main-wrapper {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
}

.cover-bg {
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.responsive-image {
    display: inline-block;
    max-width: 100%;
    height: auto;
    object-fit: cover;
}

.list-reset {
    margin: 0;
    padding: 0;
    list-style: none;
}

button {
    appearance: none;
    border: none;
    cursor: pointer;
    background-color: transparent;
}

p {
    margin-top: 0;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.4;

    @media screen and (max-width: 1024px) {
        font-size: 18px;
    }

    @media screen and (max-width: 768px) {
        font-size: 16px;
    }

    @media screen and (max-width: 670px) {
        font-size: 12px;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

iframe {
    max-width: 100%;
}

h1 {
    margin: 0;
}

li {
    color: #f3f0ec;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Turbine, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 20px;
    position: relative;
    list-style: none;
    margin-bottom: 0;
}

h2 {
    color: #17181a;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Turbine, sans-serif;
    font-size: 66px;
    font-style: normal;
    font-weight: 700;
    line-height: 1.51;
    margin-top: 0;
}

h3 {
    color: #2f3034;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Turbine, sans-serif;
    font-size: 52px;
    font-style: normal;
    font-weight: 300;
    line-height: 1.46;
    margin-top: 0;
    line-height: 1.15;

    @media screen and (max-width: 1024px) {
        font-size: 48px;
    }

    @media screen and (max-width: 768px) {
        font-size: 46px;
    }

    @media screen and (max-width: 670px) {
        font-size: 30px;
    }
}

h4 {
    color: #2f3034;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Turbine, sans-serif;
    font-size: 40px;
    font-style: normal;
    font-weight: 300;
    line-height: 1.46;
    margin-top: 0;
}

h5 {
    color: #46494d;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Turbine, sans-serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 300;
    line-height: 1.26;
    margin-top: 0;

    @media screen and (max-width: 1024px) {
        font-size: 26px;
    }

    @media screen and (max-width: 768px) {
        font-size: 18px;
    }

    @media screen and (max-width: 670px) {
        font-size: 16px;
    }
}

.blink-animation {
    animation: blink 1s infinite alternate-reverse;
}

@keyframes blink {
    from {
        opacity: 1;
        transform: scale(1, 1);
    }

    to {
        opacity: 0.5;
        transform: scale(1.1, 1.1);
    }
}


.animated-element {
    opacity: 0;
}

.from-left {
    &.is-visible {
        animation: 1s forwards linear ease-in-out;
        animation-name: from-left;
    }
}

.from-right {
    &.is-visible {
        animation: 1s forwards linear ease-in-out;
        animation-name: from-right;
    }
}

@keyframes from-right {
    from {
        opacity: 0;
        transform: translateX(100%);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes from-left {
    from {
        opacity: 0;
        transform: translateX(-100%);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

textarea,
.wpcf7-select,
input[type="text"],
input[type="tel"],
input[type="email"] {
    width: 100%;
    padding: 10px 16px;
    color: #757981;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Turbine, sans-serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 300;
    line-height: 1.2;
    appearance: none;

    border-radius: 5px;
    border: 1px solid #757981;
    background-color: #fff;
    resize: none;

    @media screen and (max-width: 768px) {
        font-size: 24px;
    }

    @media screen and (max-width: 670px) {
        font-size: 16px;
    }

    &:focus {
        outline-color: #151836
    }
}

.wpcf7-select {
    text-overflow: ellipsis;
    padding: 10px 40px 10px 10px;
    background-repeat: no-repeat;
    background-position: 99% center;
    background-size: 28px 28px;
    background-image: url(data:image/svg+xml;base64,);

    @media screen and (max-width: 670px) {
        background-size: 22px 22px;
        background-position: 97% center;
    }
}

.checkbox {
    position: relative;
    padding-left: 22px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    input {
        cursor: pointer;
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        top: 0;
        left: 0;
        z-index: 1;
    }

    .text {
        color: #1D1D1B;
        font-family: Turbine, sans-serif;
        font-size: 1.125rem;
        font-style: normal;
        font-weight: 500;
        line-height: 1;
        margin: 0;
    }

    .icon {
        position: absolute;
        top: 0;
        left: 0;
        width: 16px;
        height: 16px;
        border: 3px solid #CC7510;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
    }

    input:checked + .icon {
        background-image: url(data:image/svg+xml;base64,);
    }
}


.alignleft {
    float: left;
    margin-right: 2rem;
    margin-bottom: 2rem;
}

.alignright {
    float: right;
    margin-left: 2rem;
    margin-bottom: 2rem;
}

body.single-resources {
    background: #fff;
}